.impressum{
    color: rgba(245, 245, 245, 0.836);
    text-shadow: 1px 1px 4px black;
    display: flex;
    justify-content: center;
    max-width: 1920px;
}

.impressum_width{ 
    width: 45%;
    margin-left: 540px;
}
@media (max-width:950px) {
    .impressum{
        margin-top: 540px;
        margin-left: 0px;

    }
    .impressum_width{
        margin-left: 0px;
        width: 90%;
    }
}

@media (max-width:450px) {
.impressum{
    text-align: center;
    margin-top: 320px;
}
}